import {mapState} from 'vuex';
export default {
    name: "level",
    onLoad() { this.$commonLoad.onload();
        this.loadData();
    },
    data() {
        return {
            list: null,
            dialog: {
                show: false,
                status: 0,
                level_name: '',
                condition_text: '',
            },
            rule: {
                show: false,
                content: ''
            }
        };
    },
    computed: {
        ...mapState({
            mallConfig: state => state.mallConfig,
        }),
        dialogBg() {
            if (this.dialog.status) {
                return `background-image: url(${this.mallConfig.__wxapp_img.share.dialog_success})`;
            } else {
                return `background-image: url(${this.mallConfig.__wxapp_img.share.dialog_error})`;
            }
        },
        bg() {
            if (this.list) {
                if (this.list.length > 0) {
                    return `background-image: url(${this.mallConfig.__wxapp_img.share.sharebg})`;
                } else {
                    return `background-image: url(${this.mallConfig.__wxapp_img.share.no_level_bg})`;
                }
            } else {
                return ``;
            }
        },
    },
    methods: {
        loadData() {
            this.$showLoading();
            this.$request({
                url: this.$api.share.level,
            }).then(response => {
                this.$hideLoading();
                if (response.code == 0) {
                    this.list = response.data.list;
                } else {
                    uni.showModal({
                        content: response.msg,
                        showCancel: false
                    })
                }
            }).catch(response => {
                this.$hideLoading();
            })
        },
        levelUp() {
            uni.showLoading({
                title: '升级中...'
            });
            this.$request({
                url: this.$api.share.level_up,
            }).then(response => {
                uni.hideLoading();
                if (response.code == 0) {
                    this.dialog.show = true;
                    this.dialog = {...this.dialog, ...response.data};
                } else {
                    uni.showModal({
                        content: response.msg,
                        showCancel: false
                    })
                }
            }).catch(response => {
                uni.hideLoading();
            })
        },
        cancel() {
            this.dialog.show = false;
            if (this.dialog.status == 1) {
                this.loadData();
            }
        },
        ruleClick(item) {
            this.rule.show = true;
            this.rule.content = item.rule;
        }
    }
}
